.team__item {
  width: 100%;
  max-width: 420px;
  @media screen and (max-width: 767px) {
    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }

  .team__subtitle {
    @include font(23px, 400, 29px);
    margin: 12px 0;
    text-align: center;

    color: #333333;
  }
  .team__text {
    @include font(16px, 400, 26px);

    text-align: center;

    color: #333333;
  }
}
@media screen and (min-width: 768px) {
  .team__list {
    display: flex;
    justify-content: center;
    gap: 16px;
  }

  .team__subtitle {
    @include font(25px, 400, 32px);
  }
  .team__text {
    @include font(18px, 400, 28px);
  }
}

